.sweet-alert {
  background: linear-gradient(to bottom, #e570e7 0%, #c85ec7 47%, #a849a3 100%);
}
.sweet-alert h2 {
    color: azure;
}
.sweet-alert p {
    font-size: 1.4em;
    color: #00315e;
    text-align: left;
}
.sweet-alert button.confirm {
    background-color: #00315e !important;
    transition: background-color 0.2s;
}
.sweet-alert button.confirm:hover {
      background-color: #003e78 !important;
}
.sweet-alert .popup-content h4 {
    text-align: center;
    position: relative;
    top: -1em;
    color: antiquewhite;
}
.clean-list {
  list-style: none;
}
.img-responsive {
  max-width: 100%;
  height: auto;
  display: block;
  width: 100%;
}
ol {
  counter-reset: foo;
  display: table;
}
ol > li {
  counter-increment: foo;
  display: table-row;
}
ol > li::before {
  content: counter(foo) ".";
  display: table-cell;
  /* aha! */
  text-align: right;
}

