  --bg-color: #0067c4;
}
:root {
  /* Layout */
  --container-width: 1063px;
  --inner-container-width: 900px;
  /* Fonts */
  --primary-font: 'Rammetto One', cursive;
  --secondary-font: Ubuntu;
  /* Colors */
  --main-color: lightcyan;
  --bg-color: #0067c4;
}

/*----------  Containers  ----------*/
.container {
  width: var(--container-width);
  margin: 0 auto;
  padding: 0 1em;
}
.inner-container {
  width: var(--inner-container-width);
  margin: 0 auto;
}
@media (max-width: 1053px) {
.container,
  .inner-container {
    max-width: 98%;
}
}

/*----------  Flex Helpers  ----------*/
.flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.align-center {
  align-items: center;
}
.justify-center {
  justify-content: center;
}
.space-between {
  justify-content: space-between;
}
.flex-wrap {
  flex-wrap: wrap;
}
.full-height {
  height: 100%;
}

/* Breakpoint mixins */
[v-cloak] {
  display: none;
}
* {
  box-sizing: border-box;
}
*:focus {
  outline: none;
}
html {
  color: var(--main-color);
  background-color: var(--bg-color);
  font-family: var(--primary-font);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  text-align: center;
}
body {
  height: 100%;
}
a {
  color: currentColor;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
}
ul,
fieldset {
  padding: 0;
  margin: 0;
}
fieldset {
  border: none;
}
button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  font-family: var(--primary-font);
}
.btn {
  border: 2px solid #23436a;
  background-color: #fdd66f;
  color: #23436a;
  border-radius: 5px;
  padding: 1em;
  box-shadow: 10px 10px 5px 0px rgba(10, 10, 10, 0.75);
  transition: all .2s;
}
.btn:hover {
  cursor: pointer;
  box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.9);
}
.btn-primary {
  background-color: #543076;
  border-color: white;
  color: white;
  font-weight: bold;
}

/* Enter and leave animations can use different */
/* durations and timing functions.              */
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to {
  transform: translateX(10px);
  opacity: 0;
}



